'use client'

import { useState } from 'react'
import { ArrowRightLeft, TrendingUp, Lock, AlertCircle } from 'lucide-react'

interface FXConversionWidgetProps {
  conversionData: any
  setConversionData: (data: any) => void
}

export default function FXConversionWidget({
  conversionData,
  setConversionData
}: FXConversionWidgetProps) {
  const [isLoading, setIsLoading] = useState(false)
  const [rates, setRates] = useState({
    USD: 1550.25,
    EUR: 1680.50,
    GBP: 1950.75
  })

  // Mock live rates that update
  const getRate = (from: string, to: string) => {
    if (from === 'USD' && to === 'NGN') return rates.USD
    if (from === 'EUR' && to === 'NGN') return rates.EUR
    if (from === 'GBP' && to === 'NGN') return rates.GBP
    return 1
  }

  const currentRate = getRate(conversionData.fromCurrency, conversionData.toCurrency)
  const convertedAmount = conversionData.amount 
    ? (parseFloat(conversionData.amount) * currentRate).toLocaleString('en-US', { maximumFractionDigits: 2 })
    : '0.00'

  const handleSwap = () => {
    if (conversionData.toCurrency === 'NGN') return
    setConversionData({
      ...conversionData,
      fromCurrency: conversionData.toCurrency,
      toCurrency: conversionData.fromCurrency
    })
  }

  const handleLockRate = () => {
    setIsLoading(true)
    setTimeout(() => {
      setConversionData({
        ...conversionData,
        lockedRate: currentRate
      })
      setIsLoading(false)
    }, 500)
  }

  const handleConvert = () => {
    if (!conversionData.amount) return
    setIsLoading(true)
    setTimeout(() => {
      // Simulate API call
      setIsLoading(false)
      // Show success message
      alert(`Conversion initiated: ${conversionData.amount} ${conversionData.fromCurrency} → ₦${convertedAmount}`)
    }, 1000)
  }

  return (
    <div className="bg-white rounded-lg border border-border p-8 space-y-6">
      <div>
        <h2 className="text-2xl font-bold text-primary mb-2">Instant FX Conversion</h2>
        <p className="text-foreground/70">Enter the donation amount to see instant conversion rates</p>
      </div>

      {/* Amount Input */}
      <div className="space-y-4">
        <label className="block text-sm font-medium text-foreground">Amount to Convert</label>
        <div className="flex gap-4">
          <div className="flex-1 relative">
            <input
              type="number"
              placeholder="0.00"
              value={conversionData.amount}
              onChange={(e) => setConversionData({ ...conversionData, amount: e.target.value })}
              className="w-full px-4 py-4 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-primary/50 text-lg"
            />
            <select
              value={conversionData.fromCurrency}
              onChange={(e) => setConversionData({ ...conversionData, fromCurrency: e.target.value })}
              className="absolute right-2 top-1/2 -translate-y-1/2 bg-background border-l border-border px-2 py-1 focus:outline-none text-foreground font-medium"
            >
              <option value="USD">USD</option>
              <option value="EUR">EUR</option>
              <option value="GBP">GBP</option>
            </select>
          </div>

          <button
            onClick={handleSwap}
            disabled={conversionData.toCurrency !== 'NGN'}
            className="p-4 border border-border rounded-lg hover:bg-background disabled:opacity-50 transition-colors"
          >
            <ArrowRightLeft className="w-5 h-5 text-primary" />
          </button>

          <div className="flex-1 relative">
            <input
              type="text"
              placeholder="0.00"
              value={convertedAmount}
              readOnly
              className="w-full px-4 py-4 border border-border rounded-lg bg-background text-lg text-foreground font-medium"
            />
            <div className="absolute right-4 top-1/2 -translate-y-1/2 text-foreground font-medium">
              NGN
            </div>
          </div>
        </div>
      </div>

      {/* Rate Info */}
      <div className="grid md:grid-cols-2 gap-4 p-4 bg-background rounded-lg border border-border">
        <div>
          <p className="text-sm text-foreground/70 mb-1">Live Market Rate</p>
          <div className="flex items-center gap-2">
            <p className="text-2xl font-bold text-primary">₦{currentRate.toFixed(2)}</p>
            <span className="text-xs bg-green-100 text-green-700 px-2 py-1 rounded font-semibold">
              ↑ Real-time
            </span>
          </div>
        </div>

        {conversionData.lockedRate && (
          <div>
            <p className="text-sm text-foreground/70 mb-1">Your Locked Rate</p>
            <div className="flex items-center gap-2">
              <p className="text-2xl font-bold text-primary">₦{conversionData.lockedRate.toFixed(2)}</p>
              <span className="text-xs bg-primary/10 text-primary px-2 py-1 rounded font-semibold flex items-center gap-1">
                <Lock className="w-3 h-3" />
                Protected
              </span>
            </div>
          </div>
        )}
      </div>

      {/* Savings Indicator */}
      {conversionData.amount && (
        <div className="p-4 bg-green-50 border border-green-200 rounded-lg">
          <div className="flex items-start gap-3">
            <TrendingUp className="w-5 h-5 text-green-700 flex-shrink-0 mt-0.5" />
            <div>
              <p className="font-semibold text-green-900">You Save with Our Rates</p>
              <p className="text-sm text-green-800 mt-1">
                Compared to standard bank rates, you save approximately <span className="font-bold">₦{(parseFloat(conversionData.amount) * currentRate * 0.04).toLocaleString('en-US', { maximumFractionDigits: 0 })}</span> on this transaction
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Action Buttons */}
      <div className="flex gap-4">
        <button
          onClick={handleLockRate}
          disabled={!conversionData.amount || isLoading || conversionData.lockedRate}
          className="flex-1 flex items-center justify-center gap-2 py-4 border border-primary text-primary rounded-lg hover:bg-primary/5 disabled:opacity-50 disabled:cursor-not-allowed transition-colors font-semibold"
        >
          <Lock className="w-5 h-5" />
          {conversionData.lockedRate ? 'Rate Locked' : 'Lock This Rate'}
        </button>

        <button
          onClick={handleConvert}
          disabled={!conversionData.amount || isLoading}
          className="flex-1 py-4 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 disabled:opacity-50 transition-all font-semibold"
        >
          {isLoading ? 'Processing...' : 'Confirm Conversion'}
        </button>
      </div>

      {/* Info Box */}
      <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg flex gap-3">
        <AlertCircle className="w-5 h-5 text-blue-600 flex-shrink-0 mt-0.5" />
        <div className="text-sm text-blue-900">
          <p className="font-semibold mb-1">Pro Tip: Lock Rates for Protection</p>
          <p>Locking a rate guarantees that exchange rate for 24 hours. Perfect for large donations when you want to protect against currency fluctuations.</p>
        </div>
      </div>
    </div>
  )
}
